import React from 'react'
import { useFetchGifs } from '../hooks/useFetchGifs';
import { GifGridItem } from './GifGridItem';

export const GifGrid = ({category}) => {

    const {data: images, loading} = useFetchGifs(category);

    return (
        <>
            <h3 className="animate__animated animate__fadeIn">{category}</h3>
            <span>{loading && <p className="animate__animated animate__flash">Loading</p> }</span> {/*Si est√° en true, pon ese parrafo, sino, no hags nada*/}
            <div className="card-grid">
                {
                    images.map(img => (
                        <GifGridItem 
                            key={img.id}
                            {...img} //Estoy mandando cada una de las propiedades de manera independiente en vez de ponerimg={img} donde las agrupa todas
                        />
                    ))
                }
            </div>
        </>
    )
}
